

import React, { Component, useState } from 'react'
import moment from 'moment';
import { Select } from 'antd'
import ErrorBox from '../../components/form/error-box/error-box'
import InputType from '../../components/form/form-controls/input'
import AutoComplete from '../../components/form/form-controls/auto-complete'
import DatePicker from '../../components/form/form-controls/date-picker'
import Dropdown from '../../components/form/form-controls/select'
import Button from '../../components/form/button/button'
import Form from '../../components/form/form'
import Heading from '../../components/form/heading/heading'
import Checkbox from '../../components/form/form-controls/checkbox'
import { Nationality } from '../../helpers/nationality'

const Option = Select.Option;

function disabledDate(current) {
    // Can not select days before today and today
    return current && current > moment().endOf('day');
}

const ProfileForm = (props) => {

    const [nationality, setnationality] = useState('');
    const [panNumber, setpanNumber] = useState('');
    const [presentOccupatoin, setpresentOccupatoin] = useState('');
    const [annualIncome, setannualIncome] = useState('');
    const [dob, setdob] = useState('');
    const [typeOfCompany, settypeOfCompany] = useState('');
    const [street, setstreet] = useState('');
    const [city, setcity] = useState('');
    const [state, setstate] = useState('');
    const [zip, setzip] = useState('');
    const [declaration, setdeclaration] = useState(false);
    const [errorMsg, seterrorMsg] = useState('');
    const [zipWarn, setzipWarn] = useState(false);
    const [nationalityWarn, setnationalityWarn] = useState(false);
    const [presentOccupatoinWarn, setpresentOccupatoinWarn] = useState(false);
    const [annualIncomeWarn, setannualIncomeWarn] = useState(false);
    const [dobWarn, setdobWarn] = useState(false);
    const [typeOfCompanyWarn, settypeOfCompanyWarn] = useState(false);
    const [streetWarn, setstreetWarn] = useState(false);
    const [cityWarn, setcityWarn] = useState(false);
    const [stateWarn, setstateWarn] = useState(false);

    const onFormSubmit = (e) => {
        e.preventDefault();
        if (validate() && props.userId) {
            outlineColor()
            if (declaration) {
                const data = {
                    userId: props.userId,
                    nationality: nationality,
                    panNumber: panNumber,
                    presentOccupatoin: presentOccupatoin,
                    annualIncome: annualIncome,
                    dob: dob,
                    typeOfCompany: typeOfCompany,
                    address: {
                        street: street,
                        city: city,
                        state: state,
                        zip: zip
                    }
                }
                props.formData(data);
                seterrorMsg('')
                props.openModal(true);
            } else {
                seterrorMsg('Please accept declaration.')
            }
        } else {
            outlineColor();
            seterrorMsg('Please fill all the required field.')
        }
    }

    const outlineColor = () => {
        if (!nationality) {
            setnationalityWarn(true)
        } else {
            setnationalityWarn(false)
        }
        if (!presentOccupatoin) {
            setpresentOccupatoinWarn(true)
        } else {
            setpresentOccupatoinWarn(false)
        }
        if (!annualIncome) {
            setannualIncomeWarn(true)
        } else {
            setannualIncomeWarn(false)
        }
        if (!dob) {
            setdobWarn(true)
        } else {
            setdobWarn(false)
        }
        if (!typeOfCompany) {
            settypeOfCompanyWarn(true)
        } else {
            settypeOfCompanyWarn(false)
        }
        if (!street) {
            setstreetWarn(true)
        } else {
            setstreetWarn(false)
        }
        if (!city) {
            setcityWarn(true)
        } else {
            setcityWarn(false)
        }
        if (!state) {
            setstateWarn(true)
        } else {
            setstateWarn(false)
        }
        if (!zip) {
            setzipWarn(true)
        } else {
            setzipWarn(false)
        }
    }
    const validate = () => {
        return nationality !== '' && presentOccupatoin !== '' && annualIncome !== '' && dob !== '' && typeOfCompany !== '' && street !== '' && city !== '' && state !== '' && zip !== ''
    }
    const onInputChange = (e) => {
        e.target.name(e.target.value)
    }

    const onDateChange = (date, dateString) => {
        setdob(dateString)
    }

    const onSelect = (value) => {
        settypeOfCompany(value)
    }

    const onCheck = (e) => {
        setdeclaration(e.target.checked)
    }

    return (
        <div className="login-box">
            <Heading heading="Fill Basic Details" />
            <Form onSubmitHandler={onFormSubmit}>
                <DatePicker
                    disabledDate={disabledDate}
                    placeholder="Select Date"
                    labelName="Date Of Birth"
                    onDateChange={onDateChange}
                    isRequired={true}
                    classValue={dobWarn ? 'inputField-outline' : null}
                />
                <AutoComplete
                    onSelectChange={(value) => setnationality(value)}
                    isRequired={true}
                    name="nationality"
                    labelName="Nationality"
                    dataSource={Nationality}
                    placeholder="Select Nationality"
                    classValue={nationalityWarn ? 'inputField-outline' : null}
                />
                <InputType
                    type="text"
                    value={panNumber}
                    name="panNumber"
                    placeholder="Enter PAN Number"
                    onInputChange={onInputChange}
                    labelName="PAN Number"
                />
                <InputType
                    type="text"
                    value={presentOccupatoin}
                    name="presentOccupatoin"
                    placeholder="Enter Present Occupatoin"
                    onInputChange={onInputChange}
                    isRequired={true}
                    labelName="Present Occupatoin"
                    classValue={presentOccupatoinWarn ? 'inputField-outline' : null}
                />
                <Dropdown
                    labelName="Type of Componay"
                    placeholder="Select ToC"
                    optionFilterProp="children"
                    onSelect={onSelect}
                    size="large"
                    isRequired={true}
                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    classValue={typeOfCompanyWarn ? 'inputField-outline' : null}
                >
                    <Option value="proprietor">Proprietor</Option>
                    <Option value="partnership">Partnership</Option>
                    <Option value="pvt ltd">Pvt ltd</Option>
                </Dropdown>
                <InputType
                    type="number"
                    value={annualIncome}
                    name="annualIncome"
                    placeholder="Enter Annual Income"
                    onInputChange={onInputChange}
                    isRequired={true}
                    labelName="Annual Income"
                    classValue={annualIncomeWarn ? 'inputField-outline' : null}
                />
                <InputType
                    type="text"
                    value={street}
                    name="street"
                    placeholder="Enter Street Name"
                    onInputChange={onInputChange}
                    labelName="Street"
                    isRequired={true}
                    classValue={streetWarn ? 'inputField-outline' : null}
                />
                <InputType
                    type="text"
                    value={city}
                    name="city"
                    placeholder="Enter City Name"
                    onInputChange={onInputChange}
                    labelName="City"
                    isRequired={true}
                    classValue={cityWarn ? 'inputField-outline' : null}
                />
                <InputType
                    type="text"
                    value={state}
                    name="state"
                    placeholder="Enter State Name"
                    onInputChange={onInputChange}
                    labelName="State"
                    isRequired={true}
                    classValue={stateWarn ? 'inputField-outline' : null}
                />
                <InputType
                    type="number"
                    value={zip}
                    name="zip"
                    placeholder="Enter ZIP Code"
                    onInputChange={onInputChange}
                    labelName="ZIP Code"
                    isRequired={true}
                    classValue={zipWarn ? 'inputField-outline' : null}
                />
                <Checkbox onCheck={onCheck}>
                    Declaration: I hereby declare that the information mentioned in the application is correct &amp; true to
                    best of my knowledge.
                    </Checkbox>
                {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                <Button isType='primary' htmlTypes='submit' isBlock={true}>Create</Button>
            </Form>
        </div>
    )

}

export default ProfileForm