

import React, { Component, useState } from 'react'
import { connect } from 'react-redux'
import { Layout } from 'antd'
import Header from '../components/header/header'
import Modal from '../components/layout/modal/modal'
import PrivacyPolicy from './privacy-policy'
import ProfileForm from './profile-form/profile-form'
import { GetToken } from '../helpers/token'
import PendingComponent from './pending-component/pending-component'
// api
import createProfile from '../services/api/create-profile'


const { Content, Footer } = Layout;

const CreateProfile = (props) => {

    const [visible, setvisible] = useState(false);
    const [termCondition, settermCondition] = useState(false);
    const [errorMsg, seterrorMsg] = useState('');
    const [userData, setuserData] = useState('');
    const [showLoader, setshowLoader] = useState(false);
    const [pendingComponent, setpendingComponent] = useState(false);

    const openModal = (bool) => {
        setvisible(bool)
    }

    const callback = (data) => {
        if (data.status === 200) {
            setvisible(false)
            setshowLoader(false)
            setuserData('')
            settermCondition(false)
            setpendingComponent(true)
            console.log(data.data)
        } else if (data.response) {
            setshowLoader(false)
            seterrorMsg(data.response.data.message)
        } else {
            setshowLoader(false)
            seterrorMsg('Something went wrong. Please try again latter!')
        }
    }

    const okClick = () => {
        const token = GetToken();
        if (termCondition && userData) {
            seterrorMsg('')
            setshowLoader(true)
            userData['termCondition'] = termCondition;
            if (token) {
                createProfile(callback, userData, token)
            }
        } else {
            seterrorMsg('Please accept terms & condition.')
        }
    }

    const cancelClick = () => {
        setvisible(false)
    }

    const formData = (data) => {
        setuserData(data)
    }

    const onAccept = (e) => {
        settermCondition(e.target.checked)
    }

    return (
        <Layout>

            <Layout style={{ marginTop: 64, }}>
                <Header />
                <Modal
                    heading="Terms & Conditions"
                    showModal={visible}
                    okClick={okClick}
                    cancelClick={cancelClick}
                    okText='SUBMIT'
                >
                    <PrivacyPolicy onAccept={onAccept} errorMsg={errorMsg} showLoader={showLoader} />
                </Modal>
                {
                    pendingComponent || props.basic.approval === 'pending'
                        ? <PendingComponent />
                        :
                        <Content className="login">
                            <ProfileForm
                                openModal={openModal}
                                user={props.basic}
                                formData={formData}
                                userId={props.basic._id}
                            />
                        </Content>

                }
                <Footer style={{ textAlign: 'center' }}>
                    Copyright © 2018. Ethereal Machines Pvt Ltd. All rights reserved.
                    </Footer>
            </Layout>
        </Layout>
    )

}

function mapStateToProps(state) {
    return {
        basic: state.Basic.basic
    }
}

export default connect(mapStateToProps)(CreateProfile)
